<template>
<div>
  <div id="map" :style="MapStyle"></div>
</div>
</template>

<script>
// jquery
import $ from 'jquery';

// 
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';

// 
import {defaults as defaultControls,
        MousePosition,
        Control} from 'ol/control';

// 
import GPX from 'ol/format/GPX';
import VectorSource from 'ol/source/Vector';
import VectorLayer from 'ol/layer/Vector';

// Style
import {Style, Stroke, Fill, RegularShape} from 'ol/style';
import Shadow from 'ol-ext/style/Shadow';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import Path from 'ol-ext/featureanimation/Path';
import {linear, easwOut, easeIn, inAndOut, upAndDown} from 'ol/easing';

export default {
  name: 'one-map',
  data() {
    return {
      map: {},
      view: {},
      layers: [],
      MapStyle: {
        height: window.innerHeight + 'px',
        width: '100%'
      }
    }
  }, // data
  methods: {
    preDefine() {
      // view
      this.view = new View({
        // center: [12175093.67465372, 4209022.808896985],
        // zoom: 4.5,
        zoom: 13,
				center: [646752, 5407059],
        projection: 'EPSG:3857',
      });
      // layers
      this.layers = [
        new TileLayer({
          source: new OSM(),
          opacity: 0.7
        })
      ];
    },
    createMap() {
      this.map = new Map({
        controls: defaultControls({
          attribution: false
        }),
        target: 'map',
        view: this.view,
        layers: this.layers,
      });
      
      // 
      this.map.on('click', event => {
        console.log('(' + event.coordinate.toString() + ')');
        console.log('' + event.map.getView().getZoom());
      });
    },
    addGPXData() {
      let style = [

        // 1
        new Style({
          image: new Shadow({
            radius: 15
          }),
          stroke: new Stroke({
            color: [0,0,0,0.3],
					  width: 2
				  }),
          fill: new Fill({
            color: [0,0,0,0.3]
          }),
          zIndex: -1
        }),
        
        // 2
        new Style({
          image: new RegularShape({
            radius: 10,
            radius2: 5,
            points: 5,
            fill: new Fill({ color: 'blue' })
          }),
          stroke: new Stroke({
            color: [0,0,255],
            width: 2
          }),
          fill: new Fill({
            color: [0,0,255,0.3]
          })
        }),


      ]; // style
      style[1].getImage().getAnchor()[1] += 10;

      let source = new VectorSource({
        url: '/data/2009-09-04_rando.gpx',
        format: new GPX()
      });
      console.log(source);
      let vector = new VectorLayer({
        source: source,
        style: style
      });
      this.map.addLayer(vector);

      // Animation
      let path;
      source.once('change', (e) => {
        if (source.getState() === 'ready') {
          path = source.getFeatures()[0];
        }
      });

      //  map  feature
      let animateFeature = () => {
        if (path) {
          let f = new Feature(new Point([0,0]));
          let anim = new Path({
            path: path,
            rotate: true,
            easing: linear, // linear, easeOut, easeIn, inAndOut
            speed: Number(1.2), // min="0" max="3" step="0.1"
            revers: true
          });
          vector.animateFeature ( f, anim );
        }
      }

      for (var i=0; i<4; i++) {
        setTimeout( animateFeature, i*500 );
      }
    }
  }, // methods
  mounted() {
    const self = this;
    self.preDefine();  // 
    self.createMap();  // 

    self.addGPXData();
  }
}
</script>

